/* RecipeSearch.css */
.recipe-search {
  text-align: center;
  padding: 40px 0;
}

.search-bar {
  margin-bottom: 30px;
}

.search-input {
  padding: 12px;
  font-size: 18px;
  width: 50%;
  max-width: 400px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-right: 10px;
  box-sizing: border-box;
}

.search-button {
  padding: 12px 24px;
  font-size: 18px;
  border-radius: 5px;
  border: none;
  background-color: #ff6347;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.search-button:hover {
  background-color: #e5533f;
}

.recipe-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.recipe-card {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.recipe-card:hover {
  transform: translateY(-10px);
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
}

.recipe-title {
  font-size: 20px;
  font-weight: 600;
  color: #333;
  text-align: center;
  margin-bottom: 10px;
}

.recipe-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.recipe-card:hover .recipe-image {
  transform: scale(1.05);
}

/* RecipeDetails.css */
.star {
  cursor: pointer;
  font-size: 30px;
  color: gray;
  transition: color 0.3s;
}

.star.filled {
  color: gold;
}

