/* Registration.css */
.registration-container {
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

input {
  padding: 10px;
  margin: 10px 0;
  width: 100%;
}

button {
  padding: 10px;
  background-color: #ff6347;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

  